.stars {
  display: flex;
}

.stars__star {
  background: url(images/star.svg) center no-repeat;
  display: flex;
  height: 16px;
  width: 16px;
}

.stars__star:not(:last-of-type) {
  margin-right: 4px;
}

.stars--5 > .stars__star,
.stars--1 > .stars__star:first-of-type,
.stars--2 > .stars__star:nth-of-type(-n+2),
.stars--3 > .stars__star:nth-of-type(-n+3),
.stars--4 > .stars__star:not(:last-of-type) {
  background-image: url(images/star-active.svg);
}
